import React, { useState, useEffect } from "react";
import { gapi } from "gapi-script";
import html2canvas from "html2canvas";
import "./FuelCalculator.css";

const FuelCalculator = () => {
  const [formData, setFormData] = useState({
    kilometers: "",
    plate: "",
    unitNumber: "",
    gallonsRequired: "",
    pricePerGallon: "",
    route: "",
    company: "DUPAZA",
    indicator: 0,
    piloto: "",  // Agregar el campo piloto
  });

  const [result, setResult] = useState(null);
  const [suggestedRounding, setSuggestedRounding] = useState(null);

  // Mapa de placas, tipos de vehculos y los indicadores correspondientes
  const platesMap = {
    45: { plate: "C-516-BYN", vehicleType: "FUSO", indicator: 23 },
    46: { plate: "C-517-XYZ", vehicleType: "HINO", indicator: 18 },
    47: { plate: "C-518-ABC", vehicleType: "HYUNDAI", indicator: 20 },
  };

  // Obtener fecha y hora actual
  const currentDate = new Date();
  const currentDateString = currentDate.toLocaleDateString();
  const currentTimeString = currentDate.toLocaleTimeString();

  // Lista de pilotos desde un archivo JSON
  const [pilots, setPilots] = useState([]);

  useEffect(() => {
    // Cargar el archivo de credenciales desde la carpeta public
    fetch("/credentials/monitoreotransfam2023-776c0e4ea65f.json")
      .then((response) => response.json())
      .then((data) => {
        // Inicializar el cliente de la API de Google con las credenciales
        gapi.client.init({
          apiKey: data.apiKey,  // Asegrate de que el JSON contiene estos valores
          clientId: data.clientId,  // Asegrate de que el JSON contiene estos valores
          discoveryDocs: ["https://sheets.googleapis.com/$discovery/rest?version=v4"],
          scope: "https://www.googleapis.com/auth/spreadsheets",
        }).then(() => {
          console.log("Google API client initialized");
        });
      })
      .catch((error) => {
        console.error("Error al cargar las credenciales", error);
      });

    // Cargar la API de Google
    gapi.load("client:auth2", start);

    // Cargar los pilotos desde el archivo JSON ubicado en la carpeta public
    fetch("/pilots.json")
      .then((response) => response.json())
      .then((data) => setPilots(data))
      .catch((error) => console.error("Error al cargar los pilotos", error));
  }, []);

  // Funcin para iniciar la autenticacin con Google
  const start = () => {
    gapi.auth2.init({
      client_id: "102870671028089073255",  // Tu Client ID de OAuth 2.0
    });
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    if (name === "unitNumber") {
      const unitData = platesMap[value] || {};
      setFormData((prev) => ({
        ...prev,
        [name]: value,
        plate: unitData.plate || "No asignado",
        vehicleType: unitData.vehicleType || "Desconocido",
        indicator: unitData.indicator || 0,
      }));
    } else {
      setFormData((prev) => ({
        ...prev,
        [name]: value,
      }));
    }
  };

  const calculateFuel = () => {
    const { kilometers, indicator, pricePerGallon } = formData;
    const gallonsRequired = kilometers / indicator;
    const total = gallonsRequired * pricePerGallon;

    // Redondeo hacia el siguiente mltiplo de 5 (siempre mayor o igual)
    const roundedResult = Math.ceil(total / 5) * 5;

    setFormData((prev) => ({
      ...prev,
      gallonsRequired: gallonsRequired.toFixed(0),
    }));

    setResult(roundedResult);
    setSuggestedRounding(roundedResult);

    // Llamar a la funcin para guardar los resultados en Google Sheets
    saveToGoogleSheets(formData);
  };

  const saveToGoogleSheets = (data) => {
    const spreadsheetId = "1oJm65A12qIenGID9gwXVcfKfuwjTkPdQwEvguPr0HpU";  // Reemplaza con el ID de tu hoja de clculo de Google
    const range = "calculo!A1";  // Rango donde se guardarn los datos (ajstalo segn necesites)
    const valueInputOption = "RAW"; // Opcin de entrada de valores

    const params = {
      spreadsheetId,
      range,
      valueInputOption,
    };

    const valueRangeBody = {
      range: range,
      majorDimension: "ROWS",
      values: [
        [
          data.kilometers,
          data.plate,
          data.unitNumber,
          data.gallonsRequired,
          data.pricePerGallon,
          data.route,
          data.company,
          data.piloto,
        ],
      ],
    };

    // Llamada a la API para guardar los datos
    gapi.client.sheets.spreadsheets.values.append(params, valueRangeBody)
      .then((response) => {
        console.log("Datos guardados correctamente", response);
      })
      .catch((error) => {
        console.error("Error al guardar los datos", error);
      });
  };

  const exportToImage = () => {
    const element = document.getElementById("fuel-calculator");
    const button = document.querySelector("button");

    // Guardar estilos originales
    const originalHeight = element.style.height;
    const originalOverflow = element.style.overflow;
    const originalButtonDisplay = button.style.display;  // Guardamos el estilo original del botn

    // Expandir el contenedor dinmicamente para ajustarse a su contenido
    element.style.height = "auto"; 
    element.style.overflow = "visible"; 

    // Ocultar el botn de calcular temporalmente
    button.style.display = "none";  

    // Usamos html2canvas para capturar la imagen del contenedor
    html2canvas(element, {
      scale: 2, // Mejora la calidad de la imagen exportada
      useCORS: true, // Asegura que los recursos externos se capturen correctamente
      logging: true, // Para ver los logs y depurar problemas
      scrollX: 0,
      scrollY: -window.scrollY, // Corregir desplazamiento
      x: 0,
      y: 0,
      ignoreElements: (el) => {
        // Ignorar el campo de indicador en la exportacin
        if (el.classList.contains("indicator-container")) {
          return true;
        }
        return false;
      }
    }).then((canvas) => {
      const link = document.createElement("a");
      link.download = "fuel-calculation.jpg";
      link.href = canvas.toDataURL("image/jpeg");
      link.click();

      // Restaurar los estilos originales
      element.style.height = originalHeight;
      element.style.overflow = originalOverflow;

      // Restaurar el botn de calcular
      button.style.display = originalButtonDisplay;
    });
  };

  return (
    <div>
      <div id="fuel-calculator">
        <h2>Calculadora de Combustible</h2>

        <form onSubmit={(e) => e.preventDefault()}>
          <div className="input-container">
            <div>
              <label>Kilmetros:</label>
              <input
                type="number"
                name="kilometers"
                value={formData.kilometers}
                onChange={handleInputChange}
                required
              />
            </div>
            <div>
              <label>Indicador:</label>
              <input
                type="number"
                name="indicator"
                value={formData.indicator}
                onChange={handleInputChange}
                readOnly
              />
            </div>
          </div>

          <div className="unit-section">
            <div>
              <label>No. de Unidad:</label>
              <input
                type="number"
                name="unitNumber"
                value={formData.unitNumber}
                onChange={handleInputChange}
                required
              />
            </div>
            <div>
              <label>Placa:</label>
              <input type="text" name="plate" value={formData.plate} readOnly />
            </div>
            <div>
              <label>Tipo de Vehculo:</label>
              <input type="text" value={platesMap[formData.unitNumber]?.vehicleType || "Desconocido"} readOnly />
            </div>
          </div>

          <div>
            <label>Precio por Galn:</label>
            <input
              type="number"
              name="pricePerGallon"
              value={formData.pricePerGallon}
              onChange={handleInputChange}
              required
            />
          </div>

          <div>
            <label>Ruta:</label>
            <input
              type="text"
              name="route"
              value={formData.route}
              onChange={handleInputChange}
              required
            />
          </div>

          <div>
            <label>Compaa:</label>
            <input type="text" value={formData.company} readOnly />
          </div>

          <div>
            <label>Piloto:</label>
            <select name="piloto" onChange={handleInputChange} value={formData.piloto} required>
              {pilots.map((pilot, index) => (
                <option key={index} value={pilot.name}>{pilot.name}</option>
              ))}
            </select>
          </div>

          <button type="button" onClick={calculateFuel}>Calcular</button>
        </form>

        {result && (
          <div>
            <h3>Resultado</h3>
            <p>Total: {suggestedRounding}</p>
          </div>
        )}

        <button onClick={exportToImage}>Exportar a Imagen</button>

        {/* Botn para guardar en Google Sheets */}
        <button onClick={() => saveToGoogleSheets(formData)}>
          Guardar en Google Sheets
        </button>
      </div>
    </div>
  );
};

export default FuelCalculator;
